<script>
   /*************************************************************/
   /* Root component for the application                        */
   /*************************************************************/

   import { date2str } from '../../shared/utils';

   import TrainAppContainer from './tab_train/AppContainer.svelte';
   import TestAppContainer from './tab_test/AppContainer.svelte';
   import NewAppContainer from './tab_new/AppContainer.svelte';

   import App from '../../shared/components/MainApp.svelte';
   import WASM from '../../shared/wasm/dist/pls_wasm.js';

   /* callback for reseting the app via session */
   function resetSession(s) {
      s.train.calset = null;
      s.test.model = null;
      s.new.model = null;
      s.tab = 0;
   }

   /* callback to check validity of JSON with session */
   function checkSession(s) {

      const keys1 = Object.keys(s);
      if (!['date', 'tab', 'train', 'test', 'new'].every(v => keys1.includes(v))) return false;

      const keys2_expected = ['model', 'calset', 'calres', 'pvset', 'pvres', 'outliers', 'ncomp_selected'];
      const keys2 = Object.keys(s.train);
      if (!keys2_expected.every(v => keys2.includes(v))) return false;

      const keys3_expected = ['model', 'testset', 'testres', 'outliers', 'ncomp_selected'];
      const keys3 = Object.keys(s.test);
      if (!keys3_expected.every(v => keys3.includes(v))) return false;

      const keys4_expected = ['model', 'newset', 'newres', 'outliers'];
      const keys4 = Object.keys(s.new);
      if (!keys4_expected.every(v => keys4.includes(v))) return false;

      return true;
   }

   /* callback to initialize new session */
   function initSession() {
      return {
         date: date2str(new Date()),
         tab: 0,
         train: {
            model : 0,
            calset: 0,
            calres: 0,
            pvset: 0,
            pvres: 0,
            outliers: [],
            exclvars: [],
            ncomp_selected: 1,
         },
         test: {
            model : 0,
            testset: 0,
            testres: 0,
            outliers: [],
            ncomp_selected: 1,
         },
         new: {
            model : 0,
            newset: 0,
            newres: 0,
            outliers: [],
            ncomp_selected: 1,
         }
      }
   }

   // app containers
   const tabs = [
      {name: 'Train', label: 'train', component: TrainAppContainer},
      {name: 'Test', label: 'test', component: TestAppContainer},
      {name: 'Predict', label: 'new', component: NewAppContainer},
   ]
</script>

<App
   appID="pls"
   appTitle="PLS"
   appShortTitle="PLS"
   appSlogan="Partial least squares regression"
   {WASM}
   {tabs}
   {initSession}
   {checkSession}
   {resetSession}
>

   {#snippet footer()}
      <p>Current release: <span>1.0.0</span>
      Feedback: <a class="paper-link" href="mailto:help@mda.tools?subject=PCA app">help@mda.tools</a>
      <!--
      Tutorial: <a class="paper-link" href="Wines.zip" download>dataset</a>
      | <a href="">video lessons</a>
      -->
      </p>
   {/snippet}

   {#snippet info()}
   <h2>Partial Least Squares regression</h2>
   <p>
      Interactive web-application for creating, testing and applying PLS models for multivariate regression
      directly in your browser. The app does not send any information to the server,
      all calculations are done locally on your device.
   </p>
   <p>
      The application uses <a href="https://doi.org/10.1016/j.aca.2023.341096">Procrustes cross-validation</a> for optimization and quality check on the training stage.
   </p>
   <!--
   <p>Check out YouTube <a href="">video-lessons</a>
   -->
   <h3>v. 1.0.0</h3>
   <p class="release-info">released 17/05/2025</p>
   <ul>
      <li>Initial release with basic functionality, including removing outliers, excluding non-important variables, etc.</li>
   </ul>
   {/snippet}
</App>

